@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600;700&display=swap");

:root {
  --text: #fefbfb;
  --text-secondary: #d1d1d1;
  --background: #141414;
  --primary: #946fdc;
  --primary-translucent: rgba(148, 111, 220, 0.05);
  --secondary: #212121;
  --accent: #5e81c6;
  --accent-translucent: rgba(94, 129, 198, 0.05);
}

* {
  box-sizing: border-box;
  font-family: "Quicksand", sans-serif;
}

body {
  display: flex;
  flex-direction: column;

  margin: 0;
  padding: 0;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: var(--background);
}

.content {
  display: flex;
  flex-direction: column;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.text-gradient {
  background: -webkit-linear-gradient(0deg, var(--primary), var(--accent));
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient-outline {
  border-top: 2px solid var(--primary);
  border-bottom: 2px solid var(--accent);
  background-image: linear-gradient(var(--primary), var(--accent)),
    linear-gradient(var(--primary), var(--accent));
  background-size: 2px 100%;
  background-position: 0 0, 100% 0;
  background-repeat: no-repeat;
}

.background-gradient-linear {
  background-image: linear-gradient(180deg, var(--primary), var(--accent));
}

.padded-button {
  display: flex;
  align-items: center;
  color: var(--text);
  text-decoration: none;
  border-radius: 6px;
  padding: 6px 12px;
  cursor: pointer;
}

@keyframes levitate {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
  }
}
